
.card {
    background: #fff;
    border-radius: 2px;
    height: 100%;
    margin: 1rem;
    position: relative;
    width: 25%;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
  }
  
  .card > .img-container {
    text-align: center;
  }

  .card > .img-container > img {
    width: 200px;
    height: 200px;
  }
  
.title {
    width: 100%;
    text-align: center;
    font-size: 3rem;
    text-decoration: underline;
    color: #2A77AE;
    margin-bottom: 50px;
  }
  
.container {
    padding-top: 50px;
    background: #ECEFF1;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    padding: 20px;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
  }

  .score {
    width: 100%;
    text-align: center;
    font-size: 3rem;
    color: #2A77AE;
    margin-bottom: 50px;
  }
